import React, {useState, useEffect} from "react";
import ContainerHeader from 'components/ContainerHeader/index';
import IntlMessages from 'util/IntlMessages';
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableFooter from "@material-ui/core/TableFooter";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import PlaylistPlayIcon from "@material-ui/icons/PlaylistAdd";
import CardEditMenu from './CardEditMenu.js'
import SearchBox from '../../../../../components/SearchBox/index';
import {useSelector} from 'react-redux';
import {useHistory} from 'react-router-dom';
import CircularProgress from '@material-ui/core/CircularProgress';
import {base_url,public_url, $http} from "config";
import {Modal, ModalHeader} from 'reactstrap';
import CloseIcon from '@material-ui/icons/Close';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button';
import Fab from '@material-ui/core/Fab';
import SweetAlert from 'react-bootstrap-sweetalert'
import Accordion from '@material-ui/core/Accordion'
import { AccordionSummary, AccordionDetails, AccordionActions } from '@material-ui/core'
import Divider from '@material-ui/core/Divider';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import {NotificationContainer, NotificationManager} from 'react-notifications';

const columnData = [
  {id: "id", align: false, disablePadding: true, label: "ID"},
  {id: "building_name", align: true, disablePadding: false, label: "Building Name"},
  {id: "floors_count", align: true, disablePadding: false, label: "Floors"},
  {id: "rooms_count", align: true, disablePadding: false, label: "Rooms"},
  {id: "actions", align: true, disablePadding: false, numeric: 'right', label: "Actions"}
];
const SiteInfo = (props) => {
  const site_id = props.location.state ? props.location.state.id : null;
  const [site_open, setSiteOpen] = useState(false);
  const [building_open, setBuildingOpen] = useState(false);
  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState("id");
  const [loader, setLoader] = useState(false);
  const {width} = useSelector(({settings}) => settings);
  const History = useHistory();

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [tabledata, setTabledata] = useState([]);

  const [customername_rows, setCustomerNames] = useState([]);
  const [customer_id, setCustomerName] = useState(null);
  const [site_name, setSiteName] = useState("");
  const [site_address, setSiteAddress] = useState("");
  const [site_city, setSiteCity] = useState("");
  const [site_postcode, setSitePostcode] = useState("");
  const [site_contact_name, setSiteContactName] = useState("");
  const [out_contact, setOutContacct] = useState("");
  const [any_site, setAnySite] = useState("");
  const [hours_access, setHoursAccess] = useState("");
  const [site_comment, setSiteComment] = useState("");
  const [site,setSite] = useState([]);
  const [buildings,setBuildings] = useState([]);
  
  const [building_id, setBuildingId] = useState(null);
  const [building_name, setBuildingName] = useState("");
  const [building_upload_img, setBuildingUploadImg] = useState(null);
  const [building_upload_img_url, setBuildingUploadImgUrl] = useState('https://via.placeholder.com/300x300');
  const [goback, setGoback] = useState(false)

  useEffect(() => {
    $http.get(base_url + "site/siteInfo", {params : {id: site_id}})
    .then((response) => {
      setTabledata(response.data.buildings);
      setBuildings(response.data.buildings);
      setSite(response.data.site);
      setLoader(true);
    })
    .catch((error) => {
      setGoback(true)
    })

  }, [site_id]);

  const init_Data = () => {
    $http.get(base_url + "site/siteInfo", {params : {id: site_id}})
    .then((response) => {
      setTabledata(response.data.buildings);
      setBuildings(response.data.buildings);
      setSite(response.data.site);
      setLoader(true);
    })
    .catch((error) => {
      setGoback(true)
    })

  }

  const createSortHandler = property => event => {
    handleRequestSort(event, property);
  };

  const handleRequestSort = (event, property) => {
    let sort_order;
    if (orderBy === property && order === "asc") {
      sort_order = "desc";
    } else {
      sort_order = "asc";
    }

    
    sort_order === "desc"
      ? buildings.sort((a, b) => (b[property] < a[property] ? -1 : 1))
      : buildings.sort((a, b) => (a[property] < b[property] ? -1 : 1));

    
    setOrder(sort_order);
    setOrderBy(property);
  };

  const handleClick = (id) => {
    History.push({
      pathname: "/app/sites/mysite/building",
      state: {id: id,site_id:site_id}
    })
  };

  const handleChangePage = (event, page) => {
    setPage(page);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(event.target.value);
  };
  const onSelectBuilding = (data, index) => {
    if (index === 0) {
      History.push({
        pathname: "/app/sites/mysite/building",
        state: {id: data,site_id:site_id}
      })
    } else if (index === 1) {
      BuildingModalOpen(data);
    } else if (index === 2) {
      const frmData = new FormData();
      frmData.append('id', data);
      $http.post(base_url + "building/deleteBuilding", frmData)
      .then((response) => {
        if (response.data.status === "success") {
          init_Data();
          NotificationManager.info("You removed selected Building");
        } else if (response.data.status === "error") {
            NotificationManager.error(response.data.msg, "Error!", 1000, () => {
            });
        }
        
      }).catch((error)=>{
        NotificationManager.error(error, "Error!", 1000, () => {
        });
    })
      if (!navigator.onLine) {
        caches.open('SirvezApp').then(function(cache) {
          cache.match('/api/site/siteInfo?id='+site_id)
          .then(function(response) {return response ? response.json() : null})
          .then(function(responsedata){
              if(responsedata==null) return
              responsedata.buildings = responsedata.buildings.filter((item) => {return item.id !== data})
              const jsonResponse = new Response(JSON.stringify(responsedata), {
                headers: {'content-type': 'application/json'}
              })
              cache.put('/api/site/siteInfo?id='+site_id, jsonResponse.clone())
              .then(function(){
                init_Data()
              })
          })
        })
      }
    }
  }

  const SearchBuilding = (data) => {
    setBuildings(
      tabledata.filter(function (item) {
        return (item.building_name.toLowerCase().includes(data.toLowerCase()));
      })
    );
  }
  const SiteDelete = (data)=>{
    if (!navigator.onLine) {
      caches.open('SirvezApp').then(function(cache) {
        cache.match('/api/site/siteList')
        .then(function(response) {return response ? response.json() : null})
        .then(function(responsedata){
            if(responsedata==null) return
            responsedata.sites = responsedata.sites.filter((item) => {return item.id !== data})
            const jsonResponse = new Response(JSON.stringify(responsedata), {
              headers: {'content-type': 'application/json'}
            })
            cache.put('/api/site/siteList', jsonResponse.clone())
        })
      })
    }
    const frmData = new FormData();
    frmData.append('id', data);
    $http.post(base_url + "site/deleteSite", frmData)
    .then((response) => {
       History.push({
        pathname: "/app/sites/mysite",
        state: {}
      })

    }).catch((error)=>{
      NotificationManager.error(error, "Error!", 1000, () => {
      });
  })
    
  }
  const SiteModalOpen = (data) => {
    $http.get(base_url + "site/getSiteInfo", {params : {id: data}})
    .then((response) => {
        setCustomerNames(response.data.customers);
        if (data) {
          setCustomerName(response.data.site.company_id);
          setSiteName(response.data.site.site_name);
          setSiteAddress(response.data.site.address);
          setSiteCity(response.data.site.city);
          setSitePostcode(response.data.site.postcode);
          setSiteContactName(response.data.site.contact_name);
          setOutContacct(response.data.site.contact_number);
          setAnySite(response.data.site.site_instructions);
          setHoursAccess(response.data.site.access_hour);
          setSiteComment(response.data.site.comment);
        }
        setSiteOpen(true);
    })
    .catch((error) => {
      setGoback(true)
    })

  }
  const SiteModalClose = () => {
    setSiteOpen(false);
  }
  const SiteModalSave = () => {
    SiteModalClose();
      const frmData = new FormData();
      frmData.append('id', site_id);
      frmData.append('customer_id', customer_id);
      frmData.append('site_name', site_name);
      frmData.append('address', site_address);
      frmData.append('city', site_city);
      frmData.append('postcode', site_postcode);
      frmData.append('contact_name', site_contact_name);
      frmData.append('contact_number', out_contact);
      frmData.append('site_instructions', any_site);
      frmData.append('access_hour', hours_access);
      frmData.append('comment', site_comment);

      $http.post(base_url + "site/updateSite", frmData)
      .then((response) => {
          if (response.data.status === "success") {
              NotificationManager.success('Updated site!', "Success!", 1000, () => {
              });
              init_Data();
          } else if (response.data.status === "error") {
              NotificationManager.error(response.data.msg, "Error!", 1000, () => {
              });
          }
      }).catch((error)=>{
        NotificationManager.error(error, "Error!", 1000, () => {
        });
    })

      if (!navigator.onLine) {
        caches.open('SirvezApp').then(function(cache) {
          var modify_data = {
            id : site_id,
            customer_id : customer_id,
            site_name : site_name,
            city : site_city,
            postcode : site_postcode,
            contact_name: site_contact_name,
            contact_number: out_contact,
            site_instructions: any_site,
            access_hour: hours_access,
            comment: site_comment,
            buildings_count:0,
            floors_count:0,
            rooms_count :0,
          }
          cache.match('/api/site/getSiteInfo')
          .then(function(response) {return response ? response.json() : null})
          .then(function(responsedata){
              if(responsedata==null) return
              responsedata.site= modify_data
              const jsonResponse = new Response(JSON.stringify(responsedata), {
                headers: {'content-type': 'application/json'}
              })
              cache.put('/api/site/getSiteInfo?id='+site_id, jsonResponse.clone())
             
          })
          
          cache.match('/api/site/siteList')
          .then(function(response) {return response ? response.json() : null})
          .then(function(responsedata){
              if(responsedata==null) return
              let item = responsedata.sites.find((item) => {return item.id === site_id})
              if (item) {
                item.id = site_id
                item.customer_id = customer_id
                item.site_name = site_name
                item.city = site_city
                item.postcode = site_postcode
                item.contact_name= site_contact_name
                item.contact_number= out_contact
                item.site_instructions= any_site
                item.access_hour= hours_access
                item.comment= site_comment
                
              } else {

                  responsedata.sites.unshift(modify_data)
              }
              const jsonResponse = new Response(JSON.stringify(responsedata), {
                headers: {'content-type': 'application/json'}
              })
              cache.put('/api/site/siteList', jsonResponse.clone())
              .then(function(){
                init_Data()
              })
          })
        })
      }
  }
  const BuildingModalOpen = (data) => {
    setBuildingId(data);
    $http.get(base_url + "building/getBuildingInfo", {params : {id: data}})
    .then((response) => {
        if (data) {
          setBuildingName(response.data.building.building_name);
          setBuildingUploadImgUrl(response.data.building.upload_img ? 
            (public_url + 'upload/img/' + response.data.building.upload_img) : 
            'https://via.placeholder.com/300x300');
        }
        setBuildingOpen(true);
    })
    .catch((error) => {
      setGoback(true)
    })

  }
  const BuildingModalClose = () => {
    setBuildingOpen(false);
  }
  const BuildingModalSave = () => {
    BuildingModalClose();
    let offline_id = building_id ? building_id : JSON.parse(localStorage.getItem('user')).id + '_' + new Date().getTime()
      const frmData = new FormData();
      frmData.append('id', offline_id);
      frmData.append('site_id', site_id);
      frmData.append('building_name', building_name);
      frmData.append('upload_img', building_upload_img);
     

      $http.post(base_url + "building/updateBuilding", frmData)
      .then((response) => {
          if (response.data.status === "success") {
              NotificationManager.success('Updated building!', "Success!", 1000, () => {
              });
              init_Data();
          } else if (response.data.status === "error") {
              NotificationManager.error(response.data.msg, "Error!", 1000, () => {
              });
          }
      }).catch((error)=>{
        NotificationManager.error(error, "Error!", 1000, () => {
        });
      })

      if (!navigator.onLine) {
        caches.open('SirvezApp').then(function(cache) {
          let fileName = ''
          if(building_upload_img)
          {
              fileName = new Date().getTime()+'_new.jpeg';
              cache.put('/upload/img/'+fileName, new Response(building_upload_img));
          }
          var modify_data = {
            id : offline_id,
            site_id : site_id,
            building_name : building_name,
            rooms_count:0,
            floors_count:0,
          }
          if(fileName!=='')
          modify_data.upload_img = fileName
         
              let responsedata = {
                building : modify_data, 
                status : 'success'}
              const jsonResponse = new Response(JSON.stringify(responsedata), {
                headers: {'content-type': 'application/json'}
              })
              cache.put('/api/building/getbuildingInfo?id='+offline_id, jsonResponse.clone())
             
         

          cache.match('/api/building/buildingInfo?id='+offline_id)
          .then(function(response) {return response ? response.json() : null})
          .then(function(responsedata){
              if(responsedata==null) 
              {
                responsedata = {
                  building:modify_data,
                  status:'success',
                  floors:[],
                  rooms:[]
                }
                const jsonResponse = new Response(JSON.stringify(responsedata), {
                  headers: {'content-type': 'application/json'}
                })
                cache.put('/api/building/buildingInfo?id='+offline_id, jsonResponse.clone())
              }
              else{
                responsedata.building= modify_data
                const jsonResponse = new Response(JSON.stringify(responsedata), {
                  headers: {'content-type': 'application/json'}
                })
                cache.put('/api/building/buildingInfo?id='+offline_id, jsonResponse.clone())
              }
          })

          cache.match('/api/site/siteInfo?id='+site_id)
          .then(function(response) {return response ? response.json() : null})
          .then(function(responsedata){
              if(responsedata==null) return
              let item = responsedata.buildings.find((item) => {return item.id === offline_id})
              if (item) {
                item.site_id = site_id
                item.building_name = building_name
                if(fileName!=='')
                item.upload_img = fileName
              } else {
                  responsedata.buildings.unshift(modify_data)
              }
              const jsonResponse = new Response(JSON.stringify(responsedata), {
                headers: {'content-type': 'application/json'}
              })
              cache.put('/api/site/siteInfo?id='+site_id, jsonResponse.clone())
              .then(function(){
                init_Data()
              })
          })
        })
      }
  }
  const changeUploadImg = (e) =>{
    e.preventDefault();

    let reader = new FileReader();
    let file = e.target.files[0];
    reader.onloadend = () => {
        setBuildingUploadImg(file);
        setBuildingUploadImgUrl(reader.result);
    }
    reader.readAsDataURL(file)
}

  return (
    <div className="app-wrapper">
      <ContainerHeader match={props.match} title={<IntlMessages id="sidebar.sites.siteInfo"/>}/>
      { loader ? (
          <div className="row">
              <div className="col-12 mb-4">
                  <Accordion>
                        <AccordionSummary expandIcon={<ExpandMoreIcon/>}>
                            <div className="DetailedExpansionPanel-column-24">
                                <div className="row">
                                  <IconButton className="icon-btn" 
                                      onClick={()=>History.go(-1)}>
                                    <i className="zmdi zmdi-arrow-left"/>
                                  </IconButton>
                                  <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-6 text-left d-flex align-items-center">
                                      <h2 className="mb-0">{site.site_name}</h2>
                                  </div>
                                </div>
                            </div>
                        </AccordionSummary>
                        <AccordionDetails className="DetailedExpansionPanel-details-23">
                            <div className="DetailedExpansionPanel-column-24 conatainer">
                              <div className = "row">
                                <div className="col-xl-3 col-lg-3 col-md-3 col-sm-3 col-12 text-left">
                                </div>
                                <div className="col-xl-9 col-lg-9 col-md-9 col-sm-9 col-12 text-left">
                                  <div className="row">
                                      <div className="col-xl-4 col-lg-4 col-md-6 col-sm-4 col-4 text-left">
                                          <h2><strong>Site Name</strong></h2>
                                      </div>
                                      <div className="col-xl-8 col-lg-8 col-md-6 col-sm-8 col-8 text-left">
                                          <h2>{site.site_name}</h2>
                                      </div>
                                      <div className="col-xl-4 col-lg-4 col-md-6 col-sm-4 col-4 text-left">
                                          <h2><strong>Address</strong></h2>
                                      </div>
                                      <div className="col-xl-8 col-lg-8 col-md-6 col-sm-8 col-8 text-left">
                                          <h2>{site.address}</h2>
                                      </div>
                                      <div className="col-xl-4 col-lg-4 col-md-6 col-sm-4 col-4 text-left">
                                          <h2><strong>Contact Number</strong></h2>
                                      </div>
                                      <div className="col-xl-8 col-lg-8 col-md-6 col-sm-8 col-8 text-left">
                                          <h2>{site.contact_number}</h2>
                                      </div>
                                  </div>
                                </div>
                              </div>
                            </div>
                        </AccordionDetails>
                        <Divider/>
                        <AccordionActions>
                          <ul className="list-inline d-sm-flex flex-sm-row d-flex flex-row jr-mbtn-list mb-0 jr-featured-content-right">
                              <li><Button color="primary" onClick={() => SiteModalOpen(site_id)} variant="contained" className="jr-btn text-white"><IntlMessages id="button.modify"/></Button></li>
                              <li><Button color="secondary" onClick={() => SiteDelete()} variant="contained" className="jr-btn text-white"><IntlMessages id="button.delete"/></Button></li>
                          </ul>
                      </AccordionActions>
                    </Accordion>  
                </div>
          
              <div className="col-12">
                  <div className="jr-card">
                    <div>
                      <Toolbar className="table-header">
                        <div className="title">
                          <Typography variant="h6">Buildings</Typography>
                        </div>
                        <div className="col-md-3 col-lg-3 col-sx-6 col-6 ml-auto">
                            <SearchBox placeholder="Search ..."
                              onChange={(event) => SearchBuilding(event.target.value)}
                            />
                        </div>
                        <div className="actions">
                          <Tooltip title="New Building">
                            <IconButton aria-label="New Building" onClick={() => BuildingModalOpen(null)}>
                                <PlaylistPlayIcon/>
                            </IconButton>
                          </Tooltip>
                        </div>
                      </Toolbar>
                      {
                        loader ? (
                          <div className="flex-auto">
                            <div className="table-responsive-material">
                              <Table className="">
                                <TableHead>
                                  <TableRow>
                                    {columnData.map(column => {
                                      return (
                                        <TableCell
                                          key={column.id}
                                          align={column.numeric}
                                        >
                                          <Tooltip
                                            title="Sort"
                                            placement={column.numeric ? "bottom-end" : "bottom-start"}
                                            enterDelay={300}
                                          >
                                              <TableSortLabel
                                              active={orderBy === column.id}
                                              direction={order}
                                              onClick={createSortHandler(column.id)}
                                            >
                                              {column.label}
                                            </TableSortLabel>
                                          </Tooltip>
                                        </TableCell>
                                      );
                                    })}
                                  </TableRow>
                                </TableHead>
                                <TableBody>
                                  {
                                    buildings.length > 0 ?
                                  buildings.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((n) => 
                                      <TableRow
                                        hover
                                        key={n.id}
                                        //onClick={event => handleClick(event, n.id)}
                                        //onKeyDown={event => handleKeyDown(event, n.id)}
                                        tabIndex={-1}
                                      >
                                        <TableCell onClick={() => handleClick(n.id)}>{n.id}</TableCell>
                                        <TableCell onClick={() => handleClick(n.id)}>{n.building_name}</TableCell>
                                        <TableCell onClick={() => handleClick(n.id)}>{n.floors_count}</TableCell>
                                        <TableCell onClick={() => handleClick(n.id)}>{n.rooms_count}</TableCell>
                                        <TableCell className="text-right">
                                          <CardEditMenu key={n.id} selectedData={n.id} 
                                            onSelectRow={onSelectBuilding}/>
                                        </TableCell>
                                      </TableRow>
                                    )
                                    :
                                    <tr>
                                        <td className="text-danger" colSpan="9">
                                            <IntlMessages id="table.noData"/>
                                        </td>
                                    </tr>
                                  }
                                </TableBody>
                                <TableFooter>
                                  <TableRow>
                                    <TablePagination
                                      count={buildings.length}
                                      rowsPerPage={rowsPerPage}
                                      page={page}
                                      onChangePage={handleChangePage}
                                      onChangeRowsPerPage={handleChangeRowsPerPage}
                                    />
                                  </TableRow>
                                </TableFooter>
                              </Table>
                            </div>
                            <NotificationContainer/>
                          </div>
                        ):(
                          <div className="loader-view"
                                  style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                              <CircularProgress/>
                          </div>        
                        )
                      }
                      <Modal key="building-save" className="modal-box" toggle={BuildingModalClose} isOpen={building_open} fullwidth="true">
                          <ModalHeader>{building_id ? 'Modify Building':'Add Building'}
                              <IconButton className="text-grey"
                                          onClick={BuildingModalClose}>
                                  <CloseIcon/>
                              </IconButton>
                          </ModalHeader>
                          <div className="modal-box-content">
                              <div className="row">
                                  <div className="col-xl-3 col-lg-4 col-md-5 col-12">
                                      <div className="jr-card pb-2">
                                          <div className="jr-card-thumb">
                                              <img className="card-img-top img-fluid" alt="products" src={building_upload_img_url}/>
                                          </div>
                                          <input type="file" id="upload_img" accept="image/*" 
                                              name="upload_img" style={{display: "none"}}
                                              onChange={(event) => changeUploadImg(event)}/>

                                          <div className="jr-card-social text-right">
                                              <Fab className="jr-fab-btn bg-secondary text-white jr-btn-fab-xs mb-3" 
                                                  onClick={(e)=>{document.getElementById('upload_img').click()}}>
                                                  <i className="zmdi zmdi-cloud-upload zmdi-hc-1x"/>
                                              </Fab>
                                          </div>
                                      </div>
                                  </div>
                                  <div className="col-xl-9 col-lg-8 col-md-7 col-12">
                                    <div className="row">
                                      <div className="col-md-12 col-12">
                                          <TextField 
                                              id="building_name"
                                              label="Building Name"
                                              value={building_name}
                                              onChange={(event)=>setBuildingName(event.target.value)}
                                              margin="normal"
                                              helperText="Please Enter Building Name"
                                              fullWidth
                                              />
                                      </div>
                                  </div>
                                  </div>
                              </div>
                          </div>
                          <div className="modal-box-footer d-flex flex-row jr-featured-content-right">
                              <Button onClick={BuildingModalClose} color="secondary" variant="contained" className="jr-btn text-white">
                              Cancel
                              </Button>
                              <Button onClick={BuildingModalSave} color="primary" variant="contained" className="jr-btn text-white">
                              Save
                              </Button>
                          </div>
                          
                      </Modal>
                      <Modal key="site-save" className="modal-box" toggle={SiteModalClose} isOpen={site_open}>
                        <ModalHeader>{site_id ? 'Modify Site':'Add Site'}
                            <IconButton className="text-grey"
                                        onClick={SiteModalClose}>
                                <CloseIcon/>
                            </IconButton>
                        </ModalHeader>
                        <div className="modal-box-content">
                            <div className="row">
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="custemername"
                                        select
                                        label="Customer Name"
                                        value={customer_id}
                                        onChange={(event)=>setCustomerName(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select Customer Name"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {customername_rows.map(option => (
                                            <MenuItem key={option.id} value={option.id}>
                                                {option.name}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                
                                <div className="col-md-6 col-12">
                                    <TextField 
                                        id="site_name"
                                        label="Site"
                                        value={site_name}
                                        onChange={(event) => setSiteName(event.target.value)}
                                        margin="normal"
                                        helperText="Please Enter Site"
                                        fullWidth
                                        />
                                </div>
                              
                                <div className="col-md-6 col-12">
                                        <TextField 
                                            id="address"
                                            label="Address"
                                            value={site_address}
                                            onChange={(event)=>setSiteAddress(event.target.value)}
                                            margin="normal"
                                            helperText="Please Enter Address"
                                            fullWidth
                                            />
                                    </div>
                                    <div className="col-md-6 col-12">
                                        <TextField 
                                            id="site_city"
                                            label="City"
                                            value={site_city}
                                            onChange={(event)=>setSiteCity(event.target.value)}
                                            margin="normal"
                                            helperText="Please Enter City"
                                            fullWidth
                                            />
                                    </div>
                                    <div className="col-md-6 col-12">
                                        <TextField 
                                            id="site_postcode"
                                            label="Postcode"
                                            value={site_postcode}
                                            onChange={(event)=>setSitePostcode(event.target.value)}
                                            margin="normal"
                                            helperText="Please Enter Postcode"
                                            fullWidth
                                        />
                                    </div>
                                    <div className="col-md-6 col-12">
                                        <TextField 
                                            id="site_contact_name"
                                            label="Site Contact Name"
                                            value={site_contact_name}
                                            onChange={(event)=>setSiteContactName(event.target.value)}
                                            margin="normal"
                                            helperText="Please Enter Site Contact Name"
                                            fullWidth
                                            />
                                    </div>
                                    <div className="col-md-6 col-12">
                                        <TextField 
                                            id="out_contact"
                                            label="Out of ours contact number"
                                            value={out_contact}
                                            onChange={(event)=>setOutContacct(event.target.value)}
                                            margin="normal"
                                            helperText="Please Enter Out of ours contact number"
                                            fullWidth
                                            />
                                    </div>
                                    <div className="col-md-6 col-12">
                                        <TextField 
                                            id="any_site"
                                            label="Any site instructions"
                                            value={any_site}
                                            onChange={(event)=>setAnySite(event.target.value)}
                                            margin="normal"
                                            helperText="Please Enter Any site instructions"
                                            fullWidth
                                            />
                                    </div>
                                    <div className="col-md-6 col-12">
                                        <TextField 
                                            id="hours_access"
                                            label="Hours of access"
                                            value={hours_access}
                                            onChange={(event)=>setHoursAccess(event.target.value)}
                                            margin="normal"
                                            helperText="Please Enter Hours of access"
                                            fullWidth
                                            />
                                    </div>
                                    <div className="col-md-12 col-12">
                                        <TextField
                                            id="site_comment"
                                            label="Comments"
                                            multiline
                                            rowsMax="4"
                                            value={site_comment}
                                            onChange={(event) => setSiteComment(event.target.value)}
                                            margin="normal"
                                            fullWidth
                                        />
                                    </div>
                              
                            </div>
                        </div>
                        <div className="modal-box-footer d-flex flex-row jr-featured-content-right">
                            <Button onClick={SiteModalClose} color="secondary" variant="contained" className="jr-btn text-white">
                            Cancel
                            </Button>
                            <Button onClick={SiteModalSave} color="primary" variant="contained" className="jr-btn text-white">
                            Save
                            </Button>
                        </div>
                      </Modal>
                    </div>
                  </div>
              </div>  
          </div>
          ) :
          (
            <div className="loader-view"
                    style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                <CircularProgress/>
            </div>
          )}
          <SweetAlert show={goback}
              warning
              confirmBtnText="Go Back"
              confirmBtnBsStyle="danger"
              cancelBtnBsStyle="default"
              title="Warning!"
              onConfirm={() => History.go(-1)}
          >                
          Data not cached, so you can not view this page.
          </SweetAlert>

    </div>
  );
};

export default SiteInfo;
